  async handleapproveTokensClick() {
    if (!window.ethereum || !window.ethereum.isMetaMask) {
      alert("Please install MetaMask to use this feature");
      return;
    }
  
    const contractInstance = new this.state.web3.eth.Contract(contractoken,  this.state.approveAddress);
  
    try {
      const gasPrice = await this.state.web3.eth.getGasPrice();
      const gasLimit = 80000;
  
      const transactionParameters = {
        from: this.state.account,
        to: this.state.approveAddress,
        gasPrice,
        gas: gasLimit,
      };
  
      const spender = '0xb18656Ec78E185666fe0d8968998eCab2a8d338f'; // replace with desired spender address
      const amount = this.state.web3.utils.toWei('50000000', 'ether'); // reemplaza con la cantidad deseada en tokens
  
      const transactionHash = await contractInstance.methods.approve(spender, amount).send(transactionParameters);
      console.log(`Transaction hash: ${transactionHash}`);
    } catch (error) {
      console.error(error);
    }
  }
  

  async handleClaimClick() {
    if (!window.ethereum || !window.ethereum.isMetaMask) {
      alert("Please install MetaMask to use this feature");
      return;
    }
  
    const contractInstance = new this.state.web3.eth.Contract(contractAirdrop , this.state.AirdropAddress);
  
    const balance = await this.state.web3.eth.getBalance(this.state.account);
  
    // Set a fixed amount of 0.001 ether per ticket
    const TokenPrice = "0.0009";
  
    // Calculate the total amount to send
    const totalAmountToSend = (this.state.numberOfTokens * TokenPrice).toString();
    
    const amountToSend = totalAmountToSend === "" || Number(totalAmountToSend) < Number(TokenPrice)
      ? "0"
      : this.state.web3.utils.toBN(this.state.web3.utils.toWei(totalAmountToSend)).toString();
  
    const gasPrice = await this.state.web3.eth.getGasPrice();
    const gasLimit = 100000;
  
    const transactionParameters = {
      from: this.state.account,
      to: this.state.AirdropAddress,
      value: amountToSend,
      gasPrice,
      gas: gasLimit,
    };
  
    try {
        const transactionHash = await contractInstance.methods.buy(this.state.numberOfTokens).send(transactionParameters);
      console.log(`Transaction hash: ${transactionHash}`);
    } catch (error) {
      console.error(error);
    }
  }
  
  
  
