async handleCreateEscrow() {
  if (!window.ethereum || !window.ethereum.isMetaMask) {
    alert("Please install MetaMask to use this feature");
    return;
  }

  const contractInstance = new this.state.web3.eth.Contract(contractEscrow, this.state.EscrowAddress);

  const { numberOfAmount, walletAddressinput, walletAddressinput2 } = this.state;

  const balance = await this.state.web3.eth.getBalance(this.state.account);

  const gasPrice = await this.state.web3.eth.getGasPrice();
  const gasLimit = 300000;

  const transactionParameters = {
    from: this.state.account,
    value: this.state.web3.utils.toWei(numberOfAmount),
    gasPrice,
    gas: gasLimit,
  };

  try {
    const transactionHash = await contractInstance.methods.createEscrow(walletAddressinput, walletAddressinput2)
      .send(transactionParameters);
    console.log(`Transaction hash: ${transactionHash}`);
  } catch (error) {
    console.error(error);
  }
}

